import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
// import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

// import component
import Button from "../../../../components/uielements/button";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  Empty,
  DatePicker,
  Divider,
  Input,
  Row,
  Select,
  Tooltip
} from "antd";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";

// import action
import actions from "../../../../redux/report/accountHistoryDepartmentReport/actions";

const Option = Select.Option;
const Panel = Collapse.Panel;
const Search = Input.Search;
const RangePicker = DatePicker.RangePicker;

const redirectTo = "/dashboard/report-menus";

class AccountHistoryDepartmentReport extends Component {
  state = {
    filter: {
      start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null,
      id_department: null,
      gl_type: null,
      sort_by: "transaction_date",
      sort_type: "asc",
    },
    search: null
  };

  componentDidMount() {
    const { getAccountHistoryDepartmentReports } = this.props;
    getAccountHistoryDepartmentReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.accountHistoryDepartment.error) {
      notification("error", this.props.accountHistoryDepartment.error);
      this.props.clearError();
    }
    // Display success notif
    if (this.props.accountHistoryDepartment.success) {
      notification("success", this.props.accountHistoryDepartment.success);
      this.props.clearSuccess();
    }
  }

  // Definisi kolom2 serta field2 API yang dipakai tabel
  columns = [
    {
      title: "Account Code",
      dataIndex: "account_code",
      rowKey: "account_code"
    },
    {
      title: "Account Name",
      dataIndex: "account_name",
      rowKey: "account_name"
    },
    {
      title: "Date",
      dataIndex: "transaction_date",
      rowKey: "transaction_date",
      render: text => {
        return <span>{moment(text).format("DD MMMM YYYY")}</span>;
      }
    },
    {
      title: "Transaction Type",
      dataIndex: "transaction_type",
      rowKey: "transaction_type",
      render: text => {
        let trans_type;
        switch (text) {
          case 1:
            trans_type = "Opening Balance";
            break;
          case 2:
              trans_type = "Issue Receipt";
            break;
          case 3:
              trans_type = "Issue Receipt";
            break;
          case 4:
              trans_type = "Purchase Receipt";
            break;
          case 5:
              trans_type = "Purchase Invoice";
            break;
          case 6:
            trans_type = "Purchase Return";
            break;
          case 7:
            trans_type = "Item Transfer";
            break;
          case 8:
            trans_type = "Delivery Order";
            break;
          case 9:
            trans_type = "Sales Invoice";
            break;
          case 11:
            trans_type = "Sales Return";
            break;
          case 12:
            trans_type = "Fixed Asset";
            break;
          case 13:
            trans_type = "Journal Voucher";
            break;
          case 14:
            trans_type = "AR Payment";
            break;
          case 15:
            trans_type = "AP Payment";
            break;
          case 16:
            trans_type = "Credit Memo";
            break;
          case 17:
            trans_type = "Debit Memo";
            break;
          case 18:
            trans_type = "Cash Management";
            break;
          case 19:
            trans_type = "Job Costing";
            break;
          case 20:
            trans_type = "JC Rollover";
            break;
          default:
            trans_type = text;
        }
        return <span>{trans_type}</span>;
      }
    },
    {
      title: "Transaction No.",
      dataIndex: "transaction_no",
      rowKey: "transaction_no"
    },
    {
      title: "Department",
      dataIndex: "department.department_name",
      rowKey: "department.department_name"
    },
    {
      title: "Created By",
      dataIndex: "created_by",
      rowKey: "created_by"
    },
    {
      title: "Currency",
      dataIndex: "currency.currency_code",
      rowKey: "currency.currency_code"
    },
    {
      align: "right",
      title: "Debit",
      dataIndex: "debit",
      rowKey: "debit",
      render: (text, row) => {
        if (row.debit_credit === 1) {
          return (
            <span>
              <NumberFormat value={parseFloat(row.amount)}  />
            </span>
          );
        } else {
          return "-";
        }
      }
    },
    {
      align: "right",
      title: "Credit",
      dataIndex: "status",
      rowKey: "credit",
      render: (text, row) => {
        if (row.debit_credit === 2) {
          return (
            <span>
              <NumberFormat value={parseFloat(row.amount)}  />
            </span>
          );
        } else {
          return "-";
        }
      }
    }
  ];

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_department) {
      string += "id_department=" + filter.id_department;
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.gl_type) {
      string
        ? (string += "&gl_type=" + filter.gl_type)
        : (string += "gl_type=" + filter.gl_type);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string
  }

  // Handle filter
  handleFilter = () => {
    const { getAccountHistoryDepartmentReports } = this.props;
    let filter = this.filterValue();
    this.setState({ search: null });
    getAccountHistoryDepartmentReports(1, null, filter);
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle action di table
  handleRecord = (action, id) => {
    // console.log(action, data);
    if (action === "activate") {
      // data.is_active = 1;
      this.props.activateSupplierPriceList(id);
    } else {
      // data.is_active = 0;
      this.props.deactivateSupplierPriceList(id);
    }
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getAccountHistoryDepartmentReports } = this.props;
    this.setState({
      filter: {
        start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_department: null,
        id_location: null,
        gl_type: null,
        sort_by: "transaction_date",
        sort_type: "asc",
      }
    });
    this.setState({ search: null });
    getAccountHistoryDepartmentReports();
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getAccountHistoryDepartmentReports } = this.props;
    this.setState({
      filter: {
        start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_department: null,
        id_location: null,
        gl_type: null,
        sort_by: "transaction_date",
        sort_type: "asc",
      }
    });
    this.setState({ search: null });
    getAccountHistoryDepartmentReports();
  };

  handleSearch = value => {
    const { getAccountHistoryDepartmentReports } = this.props;
    let filter = this.filterValue();
    getAccountHistoryDepartmentReports(1, value, filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  render() {
    const { accountHistoryDepartment } = this.props;
    const {
      account_types,
      loadingForeign,
      loading,
      locations,
      departments,
      account_history_department_reports,
      filter
    } = accountHistoryDepartment;

    //console.log(accountHistoryDepartment)
    // --- init variable
    const dataSource = [...account_history_department_reports];


    // --- ambil data user dari state global:auth
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //const { current_page, page_size, total } = this.props.accountHistoryDepartment;

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="accountHistoryDepartmentReports.pageHeader" />}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="accountHistoryDepartmentReports.pageHeader" />}
              columns={this.columns}
              data={dataSource}
              filter={this.state.filter}
              locations={locations}
              departments={departments}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
            />

            <Button style={{ marginLeft:"5px" }} onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder="Account Code, Transaction No."
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
                disabled={
                  !this.state.filter.id_department
                }
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>
            <div
              style={{ 
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>

            
          </div>

          {/* Filter */}
          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="departments.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_department"
                      )}
                      value={
                        this.state.filter.id_department
                          ? this.state.filter.id_department
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.department" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                     // allowClear={true}
                      //showArrow={false}
                    >
                      {departments.map((data, key) => (
                        <Option key={key} value={data.id_department}>
                          {`${data.department_code} (${data.department_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="generalLedgers.type" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "gl_type")}
                      value={
                        this.state.filter.gl_type
                          ? this.state.filter.gl_type
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="generalLedgers.type.select" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {account_types.map((data, key) => (
                        <Option key={key} value={data.gl_type}>
                          {`${data.description}`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="start"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col> 
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "90%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="account_code">
                            <IntlMessages id="accountCode" />
                          </Option>
                          <Option key={2} value="account_name">
                            <IntlMessages id="accountName" />
                          </Option>
                          <Option key={3} value="transaction_type">
                            <IntlMessages id="transactionType" />
                          </Option>
                          <Option key={4} value="transaction_date">
                            <IntlMessages id="transactionDate" />
                          </Option>
                          <Option key={5} value="transaction_no">
                            <IntlMessages id="transactionNo" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "90%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="start"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      //allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col
                    span={4}
                    style={{
                      marginTop: "24px"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                      disabled={
                        !this.state.filter.id_department
                      }
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          {/* /Filter */}

          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="details" />
          </Divider>

          <CardWrapper>
            {!filter  
              ? (<Empty description={<IntlMessages id="noDepartment" />} />) 
              :  
                !filter.includes("id_department=") 
                  ? (<Empty description={<IntlMessages id="noDepartment" />} />)  
                  :
                    !loading && dataSource.length === 0 ? (
                      <Empty description={<IntlMessages id="noReportData" />} />
                    ) : (
                      <div className="isoInvoiceTable">
                        <Scrollbars style={{ width: "100%" }}>
                          <TableWrapper
                            bordered={true}
                            dataSource={dataSource}
                            columns={this.columns}
                            className="invoiceListTable"
                            loading={loading}
                            pagination={false}
                          />
                        </Scrollbars>
                      </div>
                    )
            }
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    accountHistoryDepartment: state.accountHistoryDepartmentReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(AccountHistoryDepartmentReport);
